import BaseScene from './base-scene';
import Button from '../objects/button';
import Globals from '../globals';
import Constants from '../constants';
import Strings from '../strings';

export default class IntroScene extends BaseScene {
    constructor() {
        super('IntroScene');
    }

    create() {
        super.create();

        this.background = this.add.image(0, 0, 'atlas', 'bg.png');

        this.platform = this.add.image(0, 0, 'atlas', 'platform_opening.png');

        this.platform.setOrigin(0, 0);
        this.add.existing(this.platform);

        // top elements
        this.gameLogo = this.add.image(0, 0, 'atlas', 'game logo.png');
        this.hebrewText = this.add.image(0, 0, 'atlas', 'hebrew_text.png');
        this.kklLogo = this.add.image(0, 0, 'atlas', 'kkl logo.png');
        this.tetrisSymbol = this.add.image(0, 0, 'atlas', 'tetris symbol.png');

        // texts
        this.titleText = this.add.text();
        this.titleText.setScale(0.5);

        this.bodyText = this.add.text();
        this.bodyText.setScale(0.5);
        this.bodyText.lineSpacing = 16;

        this.bottomText = this.add.text();
        this.bottomText.setScale(0.5);

        // buttons
        const btnLabelPadding = 8;
        const btnWidth = 156;
        const btnLabelStyle = {
            fontFamily: 'AssistantSemiBold',
            fontSize: '60px',
            fill: '#ffffff',
            align: 'center',
            fixedWidth: (btnWidth - btnLabelPadding * 2) * 2,
            wordWrap: {
                width: (btnWidth - btnLabelPadding * 2) * 2,
            },
        };

        this.playButton = new Button(
            this,
            'Vector Smart Object.png',
            'Vector Smart Object.png',
            '',
            btnLabelStyle
        );
        this.add.existing(this.playButton);
        this.playButton.label.y = -this.playButton.displayHeight * 0.42;
        this.playButton.on('pointerup', () => {
            this.cleanUp();
            this.scene.stop();
            this.scene.start('GameScene');
        });

        this.layout();
    }

    layout() {
        super.layout();

        this.background.setScale(this.scaleRatio);
        this.background.x = this.cameras.main.centerX;

        const topPadding =
            (this.game.scale.gameSize.height - this.background.displayHeight) *
            0.5;
        this.background.y = this.cameras.main.centerY + Math.abs(topPadding);

        if (this.isMobile) {
            if (this.isDeviceLandscape) {
                this.layoutMobileLandscape();
            } else {
                this.layoutMobilePortrait();
            }
        } else {
            this.layoutDestop();
        }
    }

    layoutDestop() {
        // platform
        this.platform.displayHeight = this.platform.height * this.scaleRatio;
        this.platform.displayWidth = this.platform.width * this.scaleRatio;

        this.platform.y = 0;
        this.platform.x = (this.gameWidth - this.platform.displayWidth) * 0.5;

        // top elements
        const topAvailableSpace = 180 * this.scaleRatio;
        const horizontalPadding = 40 * this.scaleRatio;
        const verticalPadding = 25 * this.scaleRatio;

        this.gameLogo.setScale(this.scaleRatio);
        this.gameLogo.x =
            this.cameras.main.centerX +
            this.gameLogo.displayWidth * 0.45;
        this.gameLogo.y = this.platform.y + topAvailableSpace * 0.5;

        this.hebrewText.setScale(this.scaleRatio);
        this.hebrewText.x =
            this.cameras.main.centerX -
            this.hebrewText.displayWidth * 0.65;
        this.hebrewText.y = this.gameLogo.y;

        this.kklLogo.setScale(this.scaleRatio);
        this.kklLogo.x =
            this.platform.x +
            this.platform.displayWidth -
            this.kklLogo.displayWidth * 0.5 - horizontalPadding;
        this.kklLogo.y = this.platform.y + topAvailableSpace * 0.5;

        // tetris symbol
        this.tetrisSymbol.setScale(this.scaleRatio);
        this.tetrisSymbol.x = this.platform.x + this.tetrisSymbol.displayWidth * 0.5 + horizontalPadding;
        this.tetrisSymbol.y = this.platform.y + this.platform.displayHeight - this.tetrisSymbol.displayHeight * 0.5 - verticalPadding;

        // texts
        const textWidth = this.platform.displayWidth - horizontalPadding * 2;
        const textLineSpacing = 12 * this.scaleRatio;
        const title = Strings.TEXT_INTRO_SCENE_TITLE;

        this.titleText.setStyle({
            fontFamily: 'AssistantBold',
            fontSize: `${Math.round(68 * this.scaleRatio)}px`,
            fontStyle: 'bold',
            fill: '#4D4749',
            rtl: true,
            fixedWidth: textWidth * 2,
            fixedHeight: 0,
            wordWrap: {
                width: textWidth * 2,
            },
        });
        this.titleText.initRTL();
        this.titleText.setText(title);

        this.titleText.x = this.platform.x + this.platform.displayWidth - horizontalPadding;
        this.titleText.y = this.platform.y + topAvailableSpace + verticalPadding;

        const body = Strings.TEXT_INTRO_SCENE_BODY_1 + '\n' +
            Strings.TEXT_INTRO_SCENE_BODY_2 + '\n' +
            Strings.TEXT_INTRO_SCENE_BODY_3 + '\n';

        this.bodyText.setStyle({
            fontFamily: 'Assistant',
            fontSize: `${Math.round(48 * this.scaleRatio)}px`,
            fill: '#4D4749',
            rtl: true,
            fixedWidth: textWidth * 2,
            fixedHeight: 0,
            wordWrap: {
                width: textWidth * 2,
            },
        });
        this.bodyText.initRTL();
        this.bodyText.setText(body);

        this.bodyText.x = this.titleText.x;
        this.bodyText.y = this.titleText.y + this.titleText.displayHeight + textLineSpacing;

        this.bottomText.setStyle({
            fontFamily: 'AssistantBold',
            fontSize: `${Math.round(52 * this.scaleRatio)}px`,
            fill: '#198EC4',
            rtl: true,
            fixedWidth: textWidth * 2,
            fixedHeight: 0,
            wordWrap: {
                width: textWidth * 2,
            },
        });
        this.bottomText.initRTL();
        this.bottomText.setText(Strings.TEXT_INTRO_LAST_LINE);

        this.bottomText.x = this.bodyText.x;
        this.bottomText.y = this.bodyText.y + this.bodyText.displayHeight - textLineSpacing * 2;

        // buttons
        this.playButton.setScale(this.scaleRatio);

        this.playButton.x = this.platform.x + this.platform.displayWidth - this.playButton.displayWidth - horizontalPadding;
        this.playButton.y = this.platform.displayHeight + this.platform.y - this.playButton.displayHeight;
    }

    layoutMobileLandscape() {
        const scale = (this.gameHeight / this.platform.height);

        // platform
        this.platform.displayWidth = this.platform.width * scale;
        this.platform.displayHeight = this.platform.height * scale * 1.25;
        this.platform.x = (this.gameWidth - this.platform.displayWidth) * 0.5;
        this.platform.y = (this.gameHeight - this.platform.displayHeight) * 0.4;

        // top elements
        const topAvailableSpace = 180 * scale * 1.25 + this.platform.y;
        const horizontalPadding = 40 * scale;
        const verticalPadding = 25 * scale;

        this.gameLogo.setScale(scale);
        this.gameLogo.x =
            this.cameras.main.centerX +
            this.gameLogo.displayWidth * 0.45;
        this.gameLogo.y = topAvailableSpace * 0.5;

        this.hebrewText.setScale(scale);
        this.hebrewText.x =
            this.cameras.main.centerX -
            this.hebrewText.displayWidth * 0.65;
        this.hebrewText.y = this.gameLogo.y;

        this.kklLogo.setScale(scale * 0.8);
        this.kklLogo.x =
            this.platform.x +
            this.platform.displayWidth -
            this.kklLogo.displayWidth * 0.5 - horizontalPadding;
        this.kklLogo.y = topAvailableSpace * 0.5;

        // tetris symbol
        this.tetrisSymbol.setScale(scale * 0.8);
        this.tetrisSymbol.x = this.platform.x + this.tetrisSymbol.displayWidth * 0.5 + horizontalPadding;
        this.tetrisSymbol.y = this.gameHeight - this.tetrisSymbol.displayHeight * 0.5 - verticalPadding;

        // texts
        const textWidth = this.platform.displayWidth - horizontalPadding * 2;
        const textLineSpacing = 12 * scale;

        this.titleText.setStyle({
            fontFamily: 'AssistantBold',
            fontSize: `${Math.round(68 * scale)}px`,
            fontStyle: 'bold',
            fill: '#4D4749',
            rtl: true,
            fixedWidth: textWidth * 2,
            fixedHeight: 0,
            wordWrap: {
                width: textWidth * 2,
            },
        });
        this.titleText.initRTL();
        this.titleText.setText(Strings.TEXT_INTRO_SCENE_TITLE);

        this.titleText.x = this.platform.x + this.platform.displayWidth - horizontalPadding;
        this.titleText.y = topAvailableSpace + verticalPadding;

        const body = Strings.TEXT_INTRO_SCENE_BODY_1 + '\n' +
            Strings.TEXT_INTRO_SCENE_BODY_2 + '\n' +
            Strings.TEXT_INTRO_SCENE_BODY_3 + '\n';

        this.bodyText.setStyle({
            fontFamily: 'Assistant',
            fontSize: `${Math.round(48 * scale)}px`,
            fill: '#4D4749',
            rtl: true,
            fixedWidth: textWidth * 2,
            fixedHeight: 0,
            wordWrap: {
                width: textWidth * 2,
            },
        });
        this.bodyText.initRTL();
        this.bodyText.setText(body);

        this.bodyText.x = this.titleText.x;
        this.bodyText.y = this.titleText.y + this.titleText.displayHeight + textLineSpacing;

        this.bottomText.setStyle({
            fontFamily: 'AssistantBold',
            fontSize: `${Math.round(52 * scale)}px`,
            fill: '#198EC4',
            rtl: true,
            fixedWidth: textWidth * 2,
            fixedHeight: 0,
            wordWrap: {
                width: textWidth * 2,
            },
        });
        this.bottomText.initRTL();
        this.bottomText.setText(Strings.TEXT_INTRO_LAST_LINE);

        this.bottomText.x = this.bodyText.x;
        this.bottomText.y = this.bodyText.y + this.bodyText.displayHeight - textLineSpacing * 2;

        // buttons
        this.playButton.setScale(scale);

        this.playButton.x = this.platform.x + this.platform.displayWidth - this.playButton.displayWidth - horizontalPadding;
        this.playButton.y = this.gameHeight - this.playButton.displayHeight;
    }

    layoutMobilePortrait() {
        const scale = this.scaleRatio;

        // platform
        this.platform.displayWidth = this.gameWidth * 0.9;
        this.platform.displayHeight = this.gameHeight * 1.25;
        this.platform.x = (this.gameWidth - this.platform.displayWidth) * 0.5;
        this.platform.y = (this.gameHeight - this.platform.displayHeight) * 0.75;

        // top elements
        const topAvailableSpace = 180 * scale * 0.75;
        const horizontalPadding = 18 * scale;
        const verticalPadding = 18 * scale;

        this.hebrewText.setScale(scale * 0.6);
        this.hebrewText.x = this.platform.x + this.hebrewText.displayWidth * 0.5 + horizontalPadding;
        this.hebrewText.y = topAvailableSpace * 0.5;

        this.kklLogo.setScale(scale * 0.7);
        this.kklLogo.x = this.platform.x + this.platform.displayWidth - this.kklLogo.displayWidth * 0.5 - horizontalPadding;
        this.kklLogo.y = topAvailableSpace * 0.5;

        this.gameLogo.setScale(scale * 0.6);
        this.gameLogo.x = (this.kklLogo.x - this.hebrewText.x) * 0.5 + this.hebrewText.x + horizontalPadding;
        this.gameLogo.y = topAvailableSpace * 0.5;

        // tetris symbol
        this.tetrisSymbol.setScale(scale * 0.8);
        this.tetrisSymbol.x = this.cameras.main.centerX;
        this.tetrisSymbol.y = this.gameHeight - this.tetrisSymbol.displayHeight * 0.5 - verticalPadding;

        // texts
        const textWidth = this.platform.displayWidth - horizontalPadding * 2;
        const textLineSpacing = 12 * scale;

        this.titleText.setStyle({
            fontFamily: 'AssistantBold',
            fontSize: `${Math.round(72 * scale)}px`,
            fontStyle: 'bold',
            fill: '#4D4749',
            rtl: true,
            fixedWidth: textWidth * 2,
            fixedHeight: 0,
            wordWrap: {
                width: textWidth * 2,
            },
        });
        this.titleText.initRTL();
        this.titleText.setText(Strings.TEXT_INTRO_SCENE_TITLE);

        this.titleText.x = this.platform.x + this.platform.displayWidth - horizontalPadding;
        this.titleText.y = topAvailableSpace + verticalPadding * 1.5;

        const body = Strings.TEXT_INTRO_SCENE_BODY_1 + '\n' +
            Strings.TEXT_INTRO_SCENE_BODY_2 + '\n' +
            Strings.TEXT_INTRO_SCENE_BODY_3 + '\n';

        this.bodyText.setStyle({
            fontFamily: 'Assistant',
            fontSize: `${Math.round(48 * scale)}px`,
            fill: '#4D4749',
            rtl: true,
            fixedWidth: textWidth * 2,
            fixedHeight: 0,
            wordWrap: {
                width: textWidth * 2,
            }
        });
        this.bodyText.initRTL();
        this.bodyText.setText(body);

        this.bodyText.x = this.titleText.x;
        this.bodyText.y = this.titleText.y + this.titleText.displayHeight + textLineSpacing;

        this.bottomText.setStyle({
            fontFamily: 'AssistantBold',
            fontSize: `${Math.round(52 * scale)}px`,
            fill: '#198EC4',
            rtl: true,
            fixedWidth: textWidth * 2,
            fixedHeight: 0,
            wordWrap: {
                width: textWidth * 2,
            },
        });
        this.bottomText.initRTL();
        this.bottomText.setText(Strings.TEXT_INTRO_LAST_LINE);

        this.bottomText.x = this.bodyText.x;
        this.bottomText.y = this.bodyText.y + this.bodyText.displayHeight - textLineSpacing * 2;

        // buttons
        this.playButton.setScale(scale);

        this.playButton.x = this.cameras.main.centerX;
        this.playButton.y = this.tetrisSymbol.y - this.tetrisSymbol.displayHeight;
    }
}