export default class Block extends Phaser.GameObjects.Container {
    constructor(scene, texture, normalFrame, correctFrame, wrongFrame) {
        super(scene);

        this._texture = texture;
        this._normalFrame = normalFrame;
        this._correctFrame = correctFrame;
        this._wrongFrame = wrongFrame;
        this._isCorrect = false;

        this.question = undefined;
        this.id = -1;
        this.column = 0;
        this.row = 0;

        this.isDropped = false;
    }

    create() {
        this._animationSprite = this.scene.make.sprite();
        this._animationSprite.x = 0;
        this._animationSprite.visible = false;
        this.add(this._animationSprite);

        this._background = this.scene.make.image();
        this._background.setTexture(this._texture);
        this._background.setFrame(this._normalFrame);
        this.add(this._background);

        this._animationSprite.y = -this._background.displayHeight * 0.85;

        this._wrongFlash = this.scene.make.image();
        this._wrongFlash.setTexture(this._texture);
        this._wrongFlash.setFrame(this._wrongFrame);
        this._wrongFlash.visible = false;
        this.add(this._wrongFlash);

        this.setSize(this._background.displayWidth, this._background.displayHeight);

        this._nameText = this.scene.make.text();
        this.add(this._nameText);

        const textWidth = this.width * 0.95;

        this._nameText.setStyle({
            fontFamily: 'AssistantBold',
            fontSize: '52px',
            fontStyle: 'bold',
            fill: '#2E3192',
            align: 'center',
            fixedWidth: textWidth * 2,
            fixedHeight: 0,
            wordWrap: {
                width: textWidth * 2,
            }
        });
        this._nameText.setScale(0.5);

        this._definitionText = this.scene.make.text();
        this.add(this._definitionText);
        this._definitionText.setScale(0.5);
    }

    layout() {
        const textWidth = this.width * 0.95;

        this._nameText.setText(this.question.term);

        this._nameText.x = -(this._nameText.displayWidth * 0.5) + 2;

        if (this._definitionText.text && this._definitionText.text.length > 0) {
            const fontFamily = this._isCorrect ? 'AssistantSemiBold' : 'AssistantBold';
            const fontSize = 40;

            this._definitionText.setStyle({
                fontFamily: fontFamily,
                fontSize: `${fontSize}px`,
                fill: '#2E3192',
                align: 'center',
                fixedWidth: textWidth * 2,
                fixedHeight: 0,
                wordWrap: {
                    width: textWidth * 2,
                }
            });

            const totalTextHeight = this._nameText.displayHeight + this._definitionText.displayHeight;
            const yPos = (this.height - totalTextHeight) * 0.5 - this.height * 0.5;

            this._nameText.y = yPos;

            this._definitionText.x = -(this._definitionText.displayWidth * 0.5) + 2;
            this._definitionText.y = this._nameText.y + this._nameText.displayHeight;
        } else {
            this._nameText.y = -this._nameText.displayHeight * 0.5;
        }
    }

    checkAnswer(answer) {
        this._isCorrect = answer === this.question.definition;

        if (this._isCorrect) {
            this._background.setFrame(this._correctFrame);
        } else {
            this.showWrongFrame();
            this.playAnimation('wrong');
        }

        if (!this.scene.isMobile) {
            this._definitionText.setText(this.question.definition);
        }

        this.layout();

        return this._isCorrect;
    }

    playAnimation(key) {
        this._animationSprite.visible = true;

        this._animationSprite.play(key);
        this._animationSprite.on('animationcomplete', () => {
            this._animationSprite.visible = false;
        });
    }

    showWrongFlash() {
        this._wrongFlash.visible = true;
        this._wrongFlash.alpha = 0;

        if (this._wrongTween) {
            this._wrongTween.destroy();
        }

        this._wrongTween = this.scene.tweens.add({
            targets: this._wrongFlash,
            alpha: 1,
            duration: 250,
            yoyo: true,
            repeat: -1
        });
    }

    showWrongFrame() {
        if (this._wrongTween) {
            this._wrongTween.remove();
            this._wrongTween = null;
        }

        this._wrongFlash.visible = false;

        this._background.setFrame(this._wrongFrame);
    }
}