import * as Phaser from 'phaser';

export default class Button extends Phaser.GameObjects.Container {
    constructor(scene, upStateKey, overStateKey, label = undefined, labelStyle = undefined) {
        super(scene);

        this.overState = new Phaser.GameObjects.Image(
            this.scene,
            0,
            0,
            'atlas',
            overStateKey
        );
        this.add(this.overState);
        this.overState.visible = false;

        this.upState = new Phaser.GameObjects.Image(
            this.scene,
            0,
            0,
            'atlas',
            upStateKey
        );
        this.add(this.upState);

        this.setSize(this.upState.width, this.upState.height);
        this.setInteractive();

        if (label !== undefined) {
            this.label = new Phaser.GameObjects.Text(this.scene, 0, 0, label, labelStyle);
            this.add(this.label);
            this.label.setScale(0.5);

            this.label.x = -this.label.displayWidth * 0.5;
            this.label.y = -this.label.displayHeight * 0.5;
        }
    }
}
