import * as Phaser from 'phaser';
import Button from './button';
import NineSlice from './nine-slice';

export default class GameRightPanel extends Phaser.GameObjects.Container {
    constructor(scene) {
        super(scene);

        this.bg = new NineSlice(
            this.scene,
            {
                texture: 'atlas',
                frame: 'panel bg.png',
                sourceWidth: 194,
                sourceHeight: 869,
                top: 20,
                right: 20,
                bottom: 20,
                left: 20,
            }
        );
        this.add(this.bg);
        this.setSize(this.bg.width, this.bg.height);

        this.dataBg = new Phaser.GameObjects.Image(
            this.scene,
            0,
            0,
            'atlas',
            'data bg.png'
        );
        this.add(this.dataBg);

        this.dataBgSingle = new NineSlice(
            this.scene,
            {
                texture: 'atlas',
                frame: 'data_bg_single.png',
                sourceWidth: 174,
                sourceHeight: 110,
                top: 20,
                right: 20,
                bottom: 20,
                left: 20,
            }
        );
        this.add(this.dataBgSingle);

        this.createTexts();

        this.downArrowBtn = this.scene.make.sprite({
            key: 'atlasNew',
            frame: 'down button.png'
        });
        this.downArrowBtn.setInteractive();
        this.add(this.downArrowBtn);

        this.leftArrowBtn = this.scene.make.sprite({
            key: 'atlasNew',
            frame: 'left button.png'
        });
        this.leftArrowBtn.setInteractive();
        this.add(this.leftArrowBtn);

        this.rightArrowBtn = this.scene.make.sprite({
            key: 'atlasNew',
            frame: 'right button.png'
        });
        this.rightArrowBtn.setInteractive();
        this.add(this.rightArrowBtn);
    }

    createTexts() {
        const labelStyle = {
            fontFamily: 'Assistant',
            fontSize: '48px',
            fill: '#666666',
            rtl: true
        };

        const valueStyle = {
            fontFamily: 'AssistantBold',
            fontSize: '74px',
            fontStyle: 'bold',
            fill: '#2E3192',
            rtl: true
        };

        this.roundLabel = new Phaser.GameObjects.Text(
            this.scene,
            0,
            0,
            ` ${2}  ${4}`,
            labelStyle
        );
        this.add(this.roundLabel);

        this.categoryNameLabel = new Phaser.GameObjects.Text(
            this.scene,
            0,
            0,
            '',
            valueStyle
        );
        this.add(this.categoryNameLabel);

        this.roundScoreLabel = new Phaser.GameObjects.Text(
            this.scene,
            0,
            0,
            '  ',
            labelStyle
        );
        this.add(this.roundScoreLabel);

        this.roundScoreValue = new Phaser.GameObjects.Text(
            this.scene,
            0,
            0,
            '0',
            valueStyle
        );
        this.add(this.roundScoreValue);

        this.totalScoreLabel = new Phaser.GameObjects.Text(
            this.scene,
            0,
            0,
            ' ',
            labelStyle
        );
        this.add(this.totalScoreLabel);

        this.totalScoreValue = new Phaser.GameObjects.Text(
            this.scene,
            0,
            0,
            '0',
            valueStyle
        );
        this.add(this.totalScoreValue);
    }

    layout(gameStage, platform) {
        if (!gameStage || !platform) {
            return;
        }

        if (this.scene.isMobile) {
            if (this.scene.isDeviceLandscape) {
                const availableSpace = platform.x + platform.displayWidth - (gameStage.x + gameStage.displayWidth * 0.5);
                let newWidth = gameStage.displayWidth * 0.35;

                this.bg.setSize9Slice(newWidth, gameStage.displayHeight);
                this.setSize(this.bg.displayWidth, this.bg.displayHeight);

                let scale = (this.bg.displayWidth / this.bg.sourceWidth) * 0.9;
                if (scale > 0.6) {
                    scale = 0.6;
                }

                this.layoutLandscape(
                    scale,
                    gameStage,
                    availableSpace
                );
            } else {
                const availableSpace = gameStage.y - gameStage.displayHeight * 0.5;
                let newHeight = gameStage.displayHeight * 0.4;

                this.bg.setSize9Slice(gameStage.displayWidth, newHeight);
                this.setSize(this.bg.displayWidth, this.bg.displayHeight);
                this.layoutPortrait(this.scene.scaleRatio, gameStage, availableSpace);
            }
        } else {
            const scale = this.scene.gameHeight / platform.height;

            this.bg.setSize9Slice(
                this.bg.sourceWidth * scale,
                this.bg.sourceHeight * scale);

            const availableSpace = platform.x + platform.displayWidth - (gameStage.x + gameStage.displayWidth * 0.5);

            this.setSize(this.bg.displayWidth, this.bg.displayHeight);
            this.layoutLandscape(scale, gameStage, availableSpace);

            const topOffset = 50 * scale;
            this.y = this.scene.cameras.main.centerY + topOffset;
        }
    }

    layoutLandscape(scale, gameStage, availableSpace) {
        this.x = gameStage.x + gameStage.displayWidth * 0.5 + availableSpace * 0.5;
        this.y = this.scene.cameras.main.centerY;

        const topMargin = 16 * scale;

        this.dataBg.setScale(scale);

        this.dataBg.x = 0;
        this.dataBg.y =
            -(this.height * 0.5) + this.dataBg.displayHeight * 0.5 + topMargin;

        this.dataBg.visible = true;
        this.dataBgSingle.visible = false;

        this.dataBgSingle.setSize9Slice(this.dataBgSingle.sourceWidth, this.dataBgSingle.sourceHeight);

        // TEXTS
        const labelPadding = 8 * scale;
        const labelTopMargin = 28 * scale;
        const valueTopMargin = 8 * scale;
        const textScale = 0.5;

        // ROUND INFO
        this.roundLabel.setScale(scale * textScale);

        this.roundLabel.x = this.roundLabel.displayWidth * 0.5;
        this.roundLabel.y =
            -(this.height * 0.5) + labelTopMargin + labelPadding;

        this.categoryNameLabel.setScale(scale * textScale);

        this.categoryNameLabel.x = this.categoryNameLabel.displayWidth * 0.5;
        this.categoryNameLabel.y =
            this.roundLabel.y + this.roundLabel.displayHeight + valueTopMargin;

        // SCORE
        this.roundScoreLabel.setScale(scale * textScale);

        this.roundScoreLabel.x = this.roundScoreLabel.displayWidth * 0.5;
        this.roundScoreLabel.y =
            -(this.height * 0.5) + labelTopMargin + labelPadding + 128 * scale;

        this.roundScoreValue.setScale(scale * textScale);

        this.roundScoreValue.x = this.roundScoreValue.displayWidth * 0.5;
        this.roundScoreValue.y =
            this.roundScoreLabel.y +
            this.roundScoreLabel.displayHeight +
            valueTopMargin;

        // TOTAL SCORE
        this.totalScoreLabel.setScale(scale * textScale);

        this.totalScoreLabel.x = this.totalScoreLabel.displayWidth * 0.5;
        this.totalScoreLabel.y =
            -(this.height * 0.5) + labelTopMargin + labelPadding + 256 * scale;

        this.totalScoreValue.setScale(scale * textScale);

        this.totalScoreValue.x = this.totalScoreValue.displayWidth * 0.5;
        this.totalScoreValue.y =
            this.totalScoreLabel.y +
            this.totalScoreLabel.displayHeight +
            valueTopMargin;

        // BUTTONS
        this.downArrowBtn.setScale(scale * (this.scene.isMobile ? 1 : 0.8));
        this.downArrowBtn.x = 0;
        this.downArrowBtn.y = this.height * 0.5 - this.downArrowBtn.displayHeight * 0.5 - valueTopMargin * 2;

        this.leftArrowBtn.setScale(scale * (this.scene.isMobile ? 1 : 0.8));
        this.leftArrowBtn.x =
            -(this.width * 0.5) +
            this.leftArrowBtn.displayWidth * 0.5 +
            labelPadding * 2;
        this.leftArrowBtn.y = this.downArrowBtn.y - this.downArrowBtn.displayHeight - valueTopMargin * 2;

        this.rightArrowBtn.setScale(scale * (this.scene.isMobile ? 1 : 0.8));
        this.rightArrowBtn.x =
            this.width * 0.5 -
            this.rightArrowBtn.displayWidth * 0.5 -
            labelPadding * 2;
        this.rightArrowBtn.y = this.leftArrowBtn.y;
    }

    layoutPortrait(scale, gameStage, availableSpace) {
        this.x = gameStage.x;
        this.y =
            (availableSpace - this.height) * 0.5 +
            gameStage.y +
            gameStage.displayHeight * 0.5 +
            this.height * 0.5;

        this.dataBg.visible = false;
        this.dataBgSingle.visible = true;

        this.dataBgSingle.setSize9Slice(
            this.width * 0.55,
            this.height * 0.9);

        const dataBgPadding = this.height - this.dataBgSingle.displayHeight;

        this.dataBgSingle.x =
            this.width * 0.5 -
            this.dataBgSingle.displayWidth * 0.5 -
            dataBgPadding * 0.5;

        // BUTTONS
        this.rightArrowBtn.setScale(scale);

        const btnAvailableSpace = this.width - this.dataBgSingle.displayWidth - dataBgPadding;
        const btnSpacing = (btnAvailableSpace - this.rightArrowBtn.displayWidth * 2) / 3;
        const btnMargin = 12 * scale;

        this.downArrowBtn.setScale(scale);
        this.downArrowBtn.x = (this.bg.x - this.bg.displayWidth * 0.5) + btnAvailableSpace * 0.5 + 6;
        this.downArrowBtn.y = this.bg.y + this.bg.displayHeight * 0.5 - this.downArrowBtn.displayHeight * 0.5 - btnMargin;

        this.rightArrowBtn.x = this.dataBgSingle.x - this.dataBgSingle.displayWidth * 0.5 - this.rightArrowBtn.displayWidth * 0.5 - btnSpacing - 2;
        this.rightArrowBtn.y = this.downArrowBtn.y - this.rightArrowBtn.displayHeight;

        this.leftArrowBtn.setScale(scale);
        this.leftArrowBtn.x = this.rightArrowBtn.x - this.rightArrowBtn.displayWidth - btnSpacing;
        this.leftArrowBtn.y = this.rightArrowBtn.y;

        // TEXTS
        const labelPadding = 8 * scale;
        const textScale = 0.5;
        const labelScale = scale * 1.1 * textScale;
        const valueScale = scale * 0.78 * textScale;

        this.roundLabel.setScale(labelScale);

        const textVerticalSpacing = (this.height - dataBgPadding - this.roundLabel.displayHeight * 3) / 4;

        this.roundLabel.x = this.width * 0.5 - dataBgPadding;// - dataBgPadding * 2;
        this.roundLabel.y = -(this.height * 0.5) + textVerticalSpacing + dataBgPadding * 0.5;

        this.categoryNameLabel.setScale(valueScale);

        this.categoryNameLabel.x =
            this.roundLabel.x - this.roundLabel.displayWidth - labelPadding;
        this.categoryNameLabel.y = this.roundLabel.y;

        // score
        this.roundScoreLabel.setScale(labelScale);

        this.roundScoreLabel.x =
            this.roundLabel.x;
        this.roundScoreLabel.y =
            this.roundLabel.y + this.roundLabel.displayHeight + textVerticalSpacing;

        this.roundScoreValue.setScale(valueScale);

        this.roundScoreValue.x = this.roundScoreLabel.x - this.roundScoreLabel.displayWidth - labelPadding;
        this.roundScoreValue.y = this.roundScoreLabel.y;

        // total score
        this.totalScoreLabel.setScale(labelScale);

        this.totalScoreLabel.x = this.roundLabel.x;
        this.totalScoreLabel.y = this.roundScoreLabel.y + this.roundScoreLabel.displayHeight + textVerticalSpacing;

        this.totalScoreValue.setScale(valueScale);

        this.totalScoreValue.x = this.totalScoreLabel.x - this.totalScoreLabel.displayWidth - labelPadding;
        this.totalScoreValue.y = this.totalScoreLabel.y;
    }

    setRoundScore(score) {
        this.roundScoreValue.setText(score);
    }

    setTotalScore(score) {
        this.totalScoreValue.setText(score);
    }

    setRoundDetails(categoryName, round, totalRounds) {
        this.categoryNameLabel.setText(categoryName);
        this.roundLabel.setText(` ${round}  ${totalRounds}`)
    }
}
