import BaseScene from './base-scene';
import GameStage from '../objects/game-stage';
import GameLeftPanel from '../objects/game-left-panel';
import GameRightPanel from '../objects/game-right-panel';
import Globals from '../globals';
import NextLevelAnimation from '../objects/next-level-animation';

export default class GameScene extends BaseScene {
    constructor() {
        super('GameScene');
    }

    create() {
        super.create();

        Globals.isGameFinished = false;

        this.background = this.add.image(0, 0, 'atlas', 'bg.png');

        this.platform = this.add.image(0, 0, 'atlas', 'platform.png');
        this.platform.setOrigin(0, 0);

        this.gameLogo = this.add.image(0, 0, 'atlas', 'game logo.png');
        this.hebrewText = this.add.image(0, 0, 'atlas', 'hebrew_text.png');
        this.kklLogo = this.add.image(0, 0, 'atlas', 'kkl logo.png');

        this.gameStage = new GameStage(this);
        this.add.existing(this.gameStage);

        this.leftPanel = new GameLeftPanel(this);
        this.add.existing(this.leftPanel);

        this.rightPanel = new GameRightPanel(this);
        this.add.existing(this.rightPanel);
        this.rightPanel.leftArrowBtn.on('pointerup', () => this.gameStage.moveBlockLeft());
        this.rightPanel.rightArrowBtn.on('pointerup', () => this.gameStage.moveBlockRight());
        this.rightPanel.downArrowBtn.on('pointerup', () => this.gameStage.moveBlockDown());

        this.input.keyboard.on('keyup_LEFT', () => this.gameStage.moveBlockLeft());
        this.input.keyboard.on('keyup_RIGHT', () => this.gameStage.moveBlockRight());
        this.input.keyboard.on('keyup_DOWN', () => this.gameStage.moveBlockDown());

        const questionsData = this.cache.json.get('questionsData');
        this.gameStage.category = questionsData.categories[Globals.round];

        this.gameStage.createQuestions();
        this.gameStage.createDefinitions();
        this.gameStage.createBlocks();

        this.rightPanel.setRoundDetails(this.gameStage.category.name,
            Globals.round + 1,
            questionsData.categories.length);

        this.createAnimations();
        this.layout();
        setTimeout(() => {
            this.gameStage.nextBlock();
        }, 500);
    }

    layout() {
        super.layout();

        // main background
        this.background.setScale(this.scaleRatio);
        this.background.x = this.cameras.main.centerX;

        const topPadding =
            (this.game.scale.gameSize.height - this.background.displayHeight) *
            0.5;
        this.background.y = this.cameras.main.centerY + Math.abs(topPadding);

        let platformScale = 1;

        // platform
        if (this.isMobile) {
            this.platform.displayWidth = this.gameWidth * 0.95;
            this.platform.displayHeight = this.gameHeight * 1.15;

            this.platform.y = this.gameHeight - this.platform.displayHeight;
        } else {
            platformScale = this.gameHeight / this.platform.height;
            this.platform.displayHeight = this.platform.height * platformScale;
            this.platform.displayWidth = this.platform.width * platformScale;

            this.platform.y = 0;
        }

        this.platform.x = (this.gameWidth - this.platform.displayWidth) * 0.5;

        const logoTopOffset = 55;
        const logoSpacing = 20;

        this.gameLogo.setScale(platformScale * 0.75);
        this.gameLogo.x =
            this.cameras.main.centerX +
            this.gameLogo.displayWidth * 0.5 +
            logoSpacing * platformScale;
        this.gameLogo.y = this.platform.y + logoTopOffset * platformScale;
        this.gameLogo.visible = !this.isMobile;

        this.hebrewText.setScale(platformScale * 0.75);
        this.hebrewText.x =
            this.cameras.main.centerX -
            this.hebrewText.displayWidth * 0.5 -
            logoSpacing * platformScale;
        this.hebrewText.y = this.platform.y + logoTopOffset * platformScale;
        this.hebrewText.visible = !this.isMobile;

        this.kklLogo.setScale(platformScale * 0.75);
        this.kklLogo.x =
            this.platform.x +
            this.platform.displayWidth -
            this.kklLogo.displayWidth * 1.75;
        this.kklLogo.y = this.platform.y + logoTopOffset * platformScale;
        this.kklLogo.visible = !this.isMobile;

        // game stage
        this.gameStage.layout(this.platform);

        // panels
        this.leftPanel.layout(this.gameStage, this.platform);
        this.rightPanel.layout(this.gameStage, this.platform);

        if (this.nextLevelAnimation) {
            this.nextLevelAnimation.layout();
        }

        if (this.finishAnimation) {
            this.finishAnimation.x = this.gameStage.x;
            this.finishAnimation.y = this.gameStage.y - 40 * this.gameStage.scaleValue;
            this.finishAnimation.setScale(3 * this.gameStage.scaleValue);
        }
    }

    update() {
        this.gameStage.update();
    }

    createAnimations() {
        this.anims.create({ key: 'correctFlag', frames: this.anims.generateFrameNames('correctFlagAtlas', { prefix: 'correct_flag_', end: 32, zeroPad: 5 }) });
        this.anims.create({ key: 'correctLamp', frames: this.anims.generateFrameNames('correctLampAtlas', { prefix: 'correct_lamp_', end: 24, zeroPad: 5 }) });
        this.anims.create({ key: 'correctLike', frames: this.anims.generateFrameNames('correctLikeAtlas', { prefix: 'correct_like_', end: 24, zeroPad: 5 }) });
        this.anims.create({ key: 'correctSmile', frames: this.anims.generateFrameNames('correctSmileAtlas', { prefix: 'correct_smile_', end: 23, zeroPad: 5 }) });
        this.anims.create({ key: 'correctStar', frames: this.anims.generateFrameNames('correctStarAtlas', { prefix: 'correct_star_', end: 23, zeroPad: 5 }) });
        this.anims.create({ key: 'correctTurbine', frames: this.anims.generateFrameNames('correctTurbineAtlas', { prefix: 'correct_turbine_', end: 24, zeroPad: 5 }) });
        this.anims.create({ key: 'finish', frames: this.anims.generateFrameNames('finishAtlas', { prefix: 'finish_', end: 124, zeroPad: 5 }) });
        this.anims.create({ key: 'wrong', frames: this.anims.generateFrameNames('wrongAtlas', { prefix: 'wrong_', end: 19, zeroPad: 5 }) });
    }

    addScore() {
        Globals.roundScore++;
        Globals.totalScore++;

        this.rightPanel.setRoundScore(Globals.roundScore);
        this.rightPanel.setTotalScore(Globals.totalScore);
    }

    replayRound() {
        if (this.nextLevelAnimation) {
            this.nextLevelAnimation.hide();
        }

        Globals.roundScore = 0;

        this.rightPanel.setRoundScore(Globals.roundScore);
        this.rightPanel.setTotalScore(Globals.totalScore);
        this.gameStage.replayRound();
        this.gameStage.layout(this.platform);
        this.gameStage.nextBlock();
    }

    replayGame() {
        if (this.nextLevelAnimation) {
            this.nextLevelAnimation.hide();
        }

        Globals.totalScore = 0;
        Globals.roundScore = 0;
        Globals.round = 0;

        const categories = this.cache.json.get('questionsData').categories;
        this.gameStage.category = categories[Globals.round];

        this.rightPanel.setRoundDetails(this.gameStage.category.name,
            Globals.round + 1,
            categories.length);
        this.rightPanel.layout(this.gameStage, this.platform);

        this.rightPanel.setRoundScore(Globals.roundScore);
        this.rightPanel.setTotalScore(Globals.totalScore);

        this.gameStage.replayGame();
        this.gameStage.layout(this.platform);
        this.gameStage.nextBlock();
    }

    nextRound() {
        Globals.round++;
        const categories = this.cache.json.get('questionsData').categories;
        Globals.roundScore = 0;

        if (Globals.round >= categories.length) {
            this.finishAnimation = this.add.sprite(0, 0);
            this.finishAnimation.x = this.gameStage.x;
            this.finishAnimation.y = this.gameStage.y - 40 * this.gameStage.scaleValue;
            this.finishAnimation.setScale(3 * this.gameStage.scaleValue);
            this.finishAnimation.play('finish');
            this.finishAnimation.on('animationcomplete', () => {
                this.finishAnimation.destroy();
                this.finishAnimation = null;

                Globals.isGameFinished = true;

                this.endGame();
            });
        } else {
            this.nextLevelAnimation = new NextLevelAnimation(this, this.gameStage);

            this.time.delayedCall(1000, () => this.nextLevelAnimation.play());

            this.time.delayedCall(3850, () => {
                this.nextLevelAnimation = null;

                this.gameStage.category = categories[Globals.round];

                this.rightPanel.setRoundDetails(this.gameStage.category.name,
                    Globals.round + 1,
                    categories.length);
                this.rightPanel.layout(this.gameStage, this.platform);

                this.rightPanel.setRoundScore(Globals.roundScore);
                this.rightPanel.setTotalScore(Globals.totalScore);

                this.gameStage.nextRound();
                this.gameStage.layout(this.platform);
                this.gameStage.nextBlock();
            });
        }
    }

    endGame() {
        this.cleanUp();
        this.scene.stop();
        this.scene.start('EndScene');
    }
}
