import * as Phaser from 'phaser';

export default class BootScene extends Phaser.Scene {
    constructor() {
        super({
            key: 'BootScene',
        });
    }

    preload() {
        // Preload the fonts
        const fonts = ['Assistant', 'AssistantSemiBold', 'AssistantBold'];

        this.fontDiv = document.createElement('div');
        document.body.appendChild(this.fontDiv);

        fonts.forEach(font => {
            const div = document.createElement('div');
            div.id = font;
            div.style.fontFamily = font;
            div.style.position = 'absolute';
            div.style.top = 0;
            div.style.left = 0;
            div.style.opacity = 0.03;
            div.innerHTML = '|Mqgy';
            this.fontDiv.appendChild(div);
        });

        // load sprite atlas
        this.load.multiatlas('atlas', 'assets/tetris-atlas.json', 'assets');

        // load questions data
        this.load.json('questionsData', 'assets/data/questions.json');

        // load atlas xml
        this.load.atlas('atlasNew', 'assets/tetris-atlas-new.png', 'assets/tetris-atlas-new.json');

        // load animations
        this.load.atlas('correctFlagAtlas', 'assets/animations/correct_flag.png', 'assets/animations/correct_flag.json');
        this.load.atlas('correctLampAtlas', 'assets/animations/correct_lamp.png', 'assets/animations/correct_lamp.json');
        this.load.atlas('correctLikeAtlas', 'assets/animations/correct_like.png', 'assets/animations/correct_like.json');
        this.load.atlas('correctSmileAtlas', 'assets/animations/correct_smile.png', 'assets/animations/correct_smile.json');
        this.load.atlas('correctStarAtlas', 'assets/animations/correct_star.png', 'assets/animations/correct_star.json');
        this.load.atlas('correctTurbineAtlas', 'assets/animations/correct_turbine.png', 'assets/animations/correct_turbine.json');
        this.load.atlas('finishAtlas', 'assets/animations/finish.png', 'assets/animations/finish.json');
        this.load.atlas('nextLevelAtlas', 'assets/animations/next_level.png', 'assets/animations/next_level.json');
        this.load.atlas('wrongAtlas', 'assets/animations/wrong.png', 'assets/animations/wrong.json');

        // load audio
        this.load.audio('backgroundMusic', 'assets/audio/040_main_loop.wav');
        this.load.audio('correctBrick', 'assets/audio/right_answer.wav');
        this.load.audio('wrongBrick', 'assets/audio/wrong_answer.wav');
        this.load.audio('gameFinish', 'assets/audio/finish_sound.mp3');
        this.load.audio('nextLevel', 'assets/audio/next_level.wav');
    }

    update() {
        this.sound.add('backgroundMusic', { loop: true }).play();

        document.body.removeChild(this.fontDiv);
        this.scene.stop();
        this.scene.start('GameScene');
    }
}
