/**
 * TODO:
 * * Set a minimum width and height
 */
export default class NineSlice extends Phaser.GameObjects.RenderTexture {
    constructor(scene, config, x = 0, y = 0, width = 32, height = 32) {
        super(scene, x, y, width, height);

        this._texture = config.texture;
        this._frame = config.frame;
        this._sourceWidth = config.sourceWidth;
        this._sourceHeight = config.sourceHeight;
        this._top = config.top;
        this._right = config.right;
        this._bottom = config.bottom;
        this._left = config.left;

        this.setOrigin(0.5);
        this._slice();
        this.setSize9Slice(this.width, this.height);
    }

    get sourceWidth() {
        return this._sourceWidth;
    }

    get sourceHeight() {
        return this._sourceHeight;
    }

    _slice() {
        this._tl = new Phaser.GameObjects.RenderTexture(
            this.scene,
            0,
            0,
            this._left,
            this._top
        );
        this._tl.drawFrame(this._texture, this._frame);

        this._l = new Phaser.GameObjects.RenderTexture(
            this.scene,
            0,
            0,
            this._left,
            this._sourceHeight - this._top - this._bottom
        );
        this._l.drawFrame(this._texture, this._frame, 0, -this._top);

        this._bl = new Phaser.GameObjects.RenderTexture(
            this.scene,
            0,
            0,
            this._left,
            this._sourceHeight - this._bottom
        );
        this._bl.drawFrame(this._texture, this._frame, 0, -(this._l.height + this._top));

        this._t = new Phaser.GameObjects.RenderTexture(
            this.scene,
            0,
            0,
            this._sourceWidth - this._left - this._right,
            this._top
        );
        this._t.drawFrame(this._texture, this._frame, -this._left, 0);

        this._c = new Phaser.GameObjects.RenderTexture(
            this.scene,
            0,
            0,
            this._sourceWidth - this._left - this._right,
            this._sourceHeight - this._top - this._bottom
        );
        this._c.drawFrame(this._texture, this._frame, -this._left, -this._top);

        this._b = new Phaser.GameObjects.RenderTexture(
            this.scene,
            0,
            0,
            this._sourceWidth - this._left - this._right,
            this._bottom
        );
        this._b.drawFrame(this._texture, this._frame, -this._left, -(this._c.height + this._top));

        this._tr = new Phaser.GameObjects.RenderTexture(
            this.scene,
            0,
            0,
            this._right,
            this._top
        );
        this._tr.drawFrame(this._texture, this._frame, -(this._sourceWidth - this._right), 0);

        this._r = new Phaser.GameObjects.RenderTexture(
            this.scene,
            0,
            0,
            this._right,
            this._sourceHeight - this._top - this._bottom
        );
        this._r.drawFrame(this._texture, this._frame, -(this._sourceWidth - this._right), -this._top);

        this._br = new Phaser.GameObjects.RenderTexture(
            this.scene,
            0,
            0,
            this._right,
            this._bottom
        );
        this._br.drawFrame(
            this._texture, this._frame,
            -(this._sourceWidth - this._right),
            -(this._sourceHeight - this._bottom));
    }

    setSize9Slice(width, height) {
        this.setSize(width, height);
        this.clear();

        // temp background
        // const rect = this.scene.add.rectangle(0, 0, this.width, this.height, 0xff0000, 0.5);
        // rect.visible = false;
        // this.draw(rect, rect.width * 0.5, rect.height * 0.5);

        // top left
        this.draw(this._tl, 0, 0);

        // left
        this._l.setScale(1, (this.height - this._top - this._bottom) / this._l.height);
        this.draw(this._l, 0, this._top);

        // bottom left
        this._bl.setScale(1)
        this.draw(this._bl, 0, this._l.displayHeight + this._top);

        // top
        this._t.setScale((this.width - this._left - this._right) / this._t.width, 1);
        this.draw(this._t, this._left, 0);

        // center
        this._c.setScale(
            (this.width - this._left - this._right) / this._c.width,
            (this.height - this._top - this._bottom) / this._c.height
        );
        this.draw(this._c, this._left, this._top);

        // bottom
        this._b.setScale((this.width - this._left - this._right) / this._b.width, 1);
        this.draw(this._b, this._left, this._c.displayHeight + this._top);

        // top right
        this.draw(this._tr, this.width - this._right, 0);

        // right
        this._r.setScale(1, (this.height - this._top - this._bottom) / this._r.height);
        this.draw(this._r, this.width - this._right, this._top);

        // bottom right
        this.draw(this._br, this.width - this._right, this.height - this._bottom);
    }
}