export default class NextLevelAnimation {
    /**
     * 
     * @param {Phaser.Scene} scene 
     */
    constructor(scene, gameStage) {
        this.scene = scene;
        this.gameStage = gameStage;

        const scale = this.gameStage.scaleValue;

        this.character = this.scene.add.sprite(0, 0, 'nextLevelAtlas', 'character');
        this.character.setScale(0.9 * scale, 0.75 * scale);
        this.character.x = gameStage.x + 120 * scale;
        this.character.y = gameStage.y - 50 * scale;
        this.character.visible = false;

        this.bubble = this.scene.add.sprite(0, 0, 'nextLevelAtlas', 'bubble');
        this.bubble.setOrigin(1, 1);
        this.bubble.setScale(0.9 * scale);
        this.bubble.x = gameStage.x + 40 * scale;
        this.bubble.y = gameStage.y - 60 * scale;
        this.bubble.visible = false;

        this.texts = [];
        this.masks = [];
    }

    play() {
        const scale = this.gameStage.scaleValue;

        this.character.visible = true;
        this.character.setScale(0.1);
        this.character.setAngle(-45);

        this.bubble.setScale(0.1);
        this.bubble.visible = false;

        this.scene.tweens.add({
            targets: this.character,
            scaleX: 0.9 * scale,
            scaleY: 0.75 * scale,
            angle: 0,
            ease: 'Power4',
            duration: 250,
            onComplete: () => {
                this.bubble.visible = true;

                this.scene.tweens.add({
                    targets: this.bubble,
                    scale: 0.9 * scale,
                    ease: 'Power4',
                    duration: 250,
                    onComplete: () => {
                        this.addTextAnimation(0, 22, -200, 170, 42, 800);
                        this.addTextAnimation(800, 22, -155, 110, 42, 500);
                        this.addTextAnimation(1300, -90, -155, 110, 42, 400);
                        this.addTextAnimation(1700, -205, -155, 88, 42, 200);

                        this.scene.time.delayedCall(2700, () => {
                            this.scene.tweens.add({
                                targets: this.character,
                                alpha: 0,
                                ease: 'Power0',
                                duration: 150
                            });

                            this.scene.tweens.add({
                                targets: this.bubble,
                                alpha: 0,
                                ease: 'Power0',
                                duration: 150,
                                onComplete: () => this.cleanUp()
                            });

                            this.texts.forEach((text) => {
                                this.scene.tweens.add({
                                    targets: text,
                                    alpha: 0,
                                    ease: 'Power0',
                                    duration: 150
                                });
                            });
                        });
                    }
                });
            }
        });
    }

    addTextAnimation(delay, x, y, width, height, duration) {
        const scale = this.gameStage.scaleValue;

        const shape = this.scene.make.graphics();
        shape.fillStyle(0xff0000);
        shape.fillRect(-width * scale, 0, width * scale, height * scale);
        shape.x = this.gameStage.x + x * scale;
        shape.y = this.gameStage.y + y * scale;
        shape.setScale(0, 1);
        shape.fillWidth = width;
        shape.fillheight = height;
        shape.xPos = x;
        shape.yPos = y;

        this.masks.push(shape);

        const mask = new Phaser.Display.Masks.GeometryMask(this.scene, shape);

        const text = this.scene.add.sprite(0, 0, 'nextLevelAtlas', 'text');
        text.setScale(0.9 * scale);
        text.x = (this.bubble.x - this.bubble.displayWidth * 0.5);
        text.y = this.bubble.y - text.displayHeight - 18 * scale;
        text.visible = false;
        text.setMask(mask);

        this.texts.push(text);

        this.scene.time.delayedCall(delay, () => {
            text.visible = true;

            this.scene.tweens.add({
                targets: shape,
                scaleX: 1,
                ease: 'Power4',
                duration: duration
            });
        });
    }

    layout() {
        const scale = this.gameStage.scaleValue;

        this.character.setScale(0.9 * scale, 0.75 * scale);
        this.character.x = this.gameStage.x + 120 * scale;
        this.character.y = this.gameStage.y - 50 * scale;

        this.bubble.setScale(0.9 * scale);
        this.bubble.x = this.gameStage.x + 40 * scale;
        this.bubble.y = this.gameStage.y - 60 * scale;

        this.masks.forEach((mask) => {
            mask.x = this.gameStage.x + mask.xPos * scale;
            mask.y = this.gameStage.y + mask.yPos * scale;
        });

        this.texts.forEach((text, index) => {
            text.setScale(0.9 * scale);
            text.x = (this.bubble.x - this.bubble.displayWidth * 0.5);
            text.y = this.bubble.y - text.displayHeight - 18 * scale;

            const shape = this.masks[index];
            const mask = new Phaser.Display.Masks.GeometryMask(this.scene, shape);

            text.setMask(mask);
        });
    }

    hide() {
        this.character.visible = false;
        this.bubble.visible = false;

        this.texts.forEach((text) => text.visible = false);
    }

    cleanUp() {
        this.character.destroy();
        this.bubble.destroy();

        this.masks.forEach((mask) => mask.destroy());
        this.texts.forEach((text) => text.destroy());

        this.masks = null;
        this.texts = null;
    }
}