import * as Phaser from 'phaser';
import Constants from '../constants';

export default class GameScene extends Phaser.Scene {
    constructor(key) {
        super({ key });
    }

    get isMobile() {
        return (
            this.sys.game.device.os.android === true ||
            this.sys.game.device.os.iOS === true
        );
    }

    get gameWidth() {
        return this.game.scale.gameSize.width;
    }

    get gameHeight() {
        return this.game.scale.gameSize.height;
    }

    get isDeviceLandscape() {
        return this.game.scale.isLandscape;
    }

    create() {
        this.scale.on('resize', this.onResize, this);
    }

    layout() {
        const gameWidth = this.game.scale.isLandscape
            ? this.gameWidth
            : this.gameHeight;
        this.scaleXRatio = gameWidth / Constants.GAME_DESIGN_WIDTH;

        const xRatio = this.gameWidth / Constants.GAME_DESIGN_WIDTH;
        const yRatio = this.gameHeight / Constants.GAME_DESIGN_HEIGHT;
        const scaleRatio = Math.max(xRatio, yRatio);

        this.scaleRatio = scaleRatio;
        this.gameScale = devicePixelRatio * scaleRatio;
    }

    cleanUp() {
        this.scale.off('resize', this.onResize, this);
    }

    onResize() {
        this.layout();
    }
}
