import * as Phaser from 'phaser';
import Button from './button';
import NineSlice from './nine-slice';
import globals from '../globals';

export default class GameLeftPanel extends Phaser.GameObjects.Container {
    constructor(scene) {
        super(scene);

        this.bg = new NineSlice(
            this.scene,
            {
                texture: 'atlas',
                frame: 'panel bg.png',
                sourceWidth: 194,
                sourceHeight: 869,
                top: 20,
                right: 20,
                bottom: 20,
                left: 20,
            }
        );
        this.add(this.bg);
        this.setSize(this.bg.width, this.bg.height);

        const btnLabelPadding = 8;
        const btnWidth = 181;
        const btnLabelStyle = {
            fontFamily: 'AssistantBold',
            fontSize: '50px',
            fill: '#2E3192',
            align: 'center',
            fixedWidth: (btnWidth - btnLabelPadding * 2) * 2,
            wordWrap: {
                width: (btnWidth - btnLabelPadding * 2) * 2,
            }
        };

        this.replayRoundBtn = new Button(
            this.scene,
            'button bg.png',
            'button bg-2.png',
            ' ',
            btnLabelStyle
        );
        this.add(this.replayRoundBtn);
        this.replayRoundBtn.on('pointerup', () => {
            this.resumeGame();
            this.scene.replayRound();
        });

        this.replayGameBtn = new Button(
            this.scene,
            'button bg.png',
            'button bg-2.png',
            ' ',
            btnLabelStyle
        );
        this.add(this.replayGameBtn);
        this.replayGameBtn.on('pointerup', () => {
            this.resumeGame();
            this.scene.replayGame();
        });

        this.endGameBtn = new Button(
            this.scene,
            'button bg.png',
            'button bg-2.png',
            ' ',
            btnLabelStyle
        );
        this.add(this.endGameBtn);
        this.endGameBtn.on('pointerup', () => {
            this.resumeGame();
            this.scene.endGame();
        });

        this.toggleSoundBtn = this.scene.make.sprite({
            x: 0,
            y: 0,
            key: 'atlasNew',
            frame: globals.isMute ? 'sound off.png' : 'sound on.png'
        });
        this.add(this.toggleSoundBtn);
        this.toggleSoundBtn.setInteractive();
        this.toggleSoundBtn.on('pointerup', () => {
            if (globals.isGamePaused) {
                return;
            }

            globals.isMute = !globals.isMute;

            this.scene.sound.setMute(globals.isMute);
            this.toggleSoundBtn.setFrame(globals.isMute ? 'sound off.png' : 'sound on.png');
        });

        this.pauseGameBtn = this.scene.make.sprite({
            x: 0,
            y: 0,
            key: 'atlasNew',
            frame: 'pause.png'
        });
        this.add(this.pauseGameBtn);
        this.pauseGameBtn.setInteractive();
        this.pauseGameBtn.on('pointerup', () => {
            globals.isGamePaused = !globals.isGamePaused;

            this.pauseGameBtn.setFrame(globals.isGamePaused ? 'play.png' : 'pause.png');

            if (globals.isGamePaused) {
                this.pauseGameBtn.setFrame('play.png');
                this.scene.sound.setMute(true);
            } else {
                this.pauseGameBtn.setFrame('pause.png');

                if (!globals.isMute) {
                    this.scene.sound.setMute(false);
                }
            }
        });
    }

    resumeGame() {
        globals.isGamePaused = false;
        this.pauseGameBtn.setFrame('pause.png');

        if (!globals.isMute) {
            this.scene.sound.setMute(false);
        }
    }

    layout(gameStage, platform) {
        if (!gameStage || !platform) {
            return;
        }

        if (this.scene.isMobile) {
            if (this.scene.isDeviceLandscape) {
                const availableSpace = gameStage.x - gameStage.displayWidth * 0.5 - platform.x;
                let newWidth = gameStage.displayWidth * 0.35;

                this.bg.setSize9Slice(newWidth, gameStage.displayHeight);
                this.setSize(this.bg.displayWidth, this.bg.displayHeight);
                this.layoutLandscape(
                    (this.bg.displayWidth / this.bg.sourceWidth) * 0.9,
                    platform,
                    availableSpace
                );
            } else {
                const availableSpace =
                    this.scene.gameHeight -
                    (gameStage.y + gameStage.displayHeight * 0.5);
                // let newHeight = availableSpace < 100 ? availableSpace : availableSpace * 0.9;
                let newHeight = gameStage.displayHeight * 0.4;

                this.bg.setSize9Slice(gameStage.displayWidth, newHeight);
                this.setSize(this.bg.displayWidth, this.bg.displayHeight);
                this.layoutPortrait(this.scene.scaleRatio * 0.9, gameStage, availableSpace);
            }
        } else {
            const scale = this.scene.gameHeight / platform.height;

            this.bg.setSize9Slice(
                this.bg.sourceWidth * scale,
                this.bg.sourceHeight * scale);

            const availableSpace =
                gameStage.x - gameStage.displayWidth * 0.5 - platform.x;

            this.setSize(this.bg.displayWidth, this.bg.displayHeight);
            this.layoutLandscape(scale, platform, availableSpace);

            const topOffset = 50 * scale;
            this.y = this.scene.cameras.main.centerY + topOffset;
        }
    }

    layoutLandscape(scale, platform, availableSpace) {
        this.x = platform.x + availableSpace * 0.5;
        this.y = this.scene.cameras.main.centerY;

        // BUTTONS
        const btnVerticalSpacing = 4 * scale;

        this.replayRoundBtn.setScale(scale);

        this.replayRoundBtn.x = 0;
        this.replayRoundBtn.y =
            -(this.height * 0.5) +
            this.replayRoundBtn.displayHeight * 0.5 +
            btnVerticalSpacing * 3;;

        this.replayGameBtn.setScale(scale);

        this.replayGameBtn.x = 0;
        this.replayGameBtn.y =
            this.replayRoundBtn.y +
            this.replayGameBtn.displayHeight +
            btnVerticalSpacing;

        this.endGameBtn.setScale(scale);

        this.endGameBtn.x = 0;
        this.endGameBtn.y =
            this.replayGameBtn.y +
            this.endGameBtn.displayHeight +
            btnVerticalSpacing;

        this.toggleSoundBtn.setScale(scale);

        this.toggleSoundBtn.x = 0;
        this.toggleSoundBtn.y = this.displayHeight * 0.5 - this.toggleSoundBtn.displayHeight * 0.5 - btnVerticalSpacing * 6;

        this.pauseGameBtn.setScale(scale);

        this.pauseGameBtn.x = this.toggleSoundBtn.x;
        this.pauseGameBtn.y = this.toggleSoundBtn.y - this.pauseGameBtn.displayHeight - btnVerticalSpacing * 6;
    }

    layoutPortrait(scale, gameStage, availableSpace) {
        this.x = gameStage.x;
        this.y = availableSpace * 0.5;

        // BUTTONS
        this.toggleSoundBtn.setScale(scale);

        const btnSpacing = (this.height - this.toggleSoundBtn.displayHeight * 2) / 3;

        this.toggleSoundBtn.x = this.bg.x - this.bg.displayWidth * 0.5 + this.toggleSoundBtn.displayWidth * 0.5 + btnSpacing;
        this.toggleSoundBtn.y = this.bg.y + this.bg.displayHeight * 0.5 - this.toggleSoundBtn.displayHeight * 0.5 - btnSpacing;

        this.pauseGameBtn.setScale(scale);

        this.pauseGameBtn.x = this.toggleSoundBtn.x;
        this.pauseGameBtn.y = this.toggleSoundBtn.y - this.pauseGameBtn.displayHeight - btnSpacing;

        this.replayRoundBtn.setScale(scale);

        this.replayRoundBtn.x = this.width * 0.5 - this.replayRoundBtn.displayWidth * 0.5 - btnSpacing;
        this.replayRoundBtn.y = this.pauseGameBtn.y;

        this.replayGameBtn.setScale(scale);

        this.replayGameBtn.x = this.replayRoundBtn.x;
        this.replayGameBtn.y = this.replayRoundBtn.y + this.replayGameBtn.displayHeight + btnSpacing;

        this.endGameBtn.setScale(scale);

        this.endGameBtn.x = this.replayRoundBtn.x - this.endGameBtn.displayWidth - btnSpacing;
        this.endGameBtn.y = this.replayRoundBtn.y;
    }
}
